import tagSexAge from '@/components/common/tag-sex-age.vue'
export default {
	props:{
		item: Object,
		index: Number,
		index1: {
			type: Number,
			default: undefined
		},
		itemClass: {
			type: String,
			default: 'fadeIn'
		}
	},
	data () {
		return {}
	},
	components: {
		tagSexAge
	},
	computed: {
		getSex() {
			return this.item.sex;
		},
		getAge(){
			return this.item.age;
		}
	},
	methods: {
		async follow () {
			try{
				let [err,res] = await this.$http.post('/follow',{
					follow_id:this.item.userid
				},{
					token:true,
					checkToken:true,
					checkAuth:true
				});
				// 错误处理
				if (!this.$http.errorCheck(err,res)) return;
				// 通知首页修改数据
				uni.showToast({ title: '关注成功' });
				let resdata = {
					type:"guanzhu",
					userid:this.item.userid,
					data:true
				};
				// 通知父组件
				this.$emit('changeEvent',resdata);
				// 全局通知
				uni.$emit('updateData',resdata);
			}catch(e){ return; }
		},
		// 进入详情页
		opendetail(){
			uni.navigateTo({
				url: '../../pages/detail/detail?detailData='+JSON.stringify(this.item),
			});
			this.User.addHistoryList(this.item);
		},
		async caozuo(type){
			let index = (type === 'smile') ? 1 : 2; // 当前操作
			let [err,res] = await this.$http.post('/support',{
				post_id:this.item.id,
				type:index-1
			},{
				token:true,
				checkToken:true,
				checkAuth:true
			});
			if (!this.$http.errorCheck(err,res)) return;
			// console.log(res);
			uni.showToast({ title: "顶成功" });
			// 通知父组件
			let resdata = {
				type:"support",
				post_id:this.item.id,
				do:type
			};
			this.$emit('changeEvent',resdata);
			// 通知全局
			return uni.$emit("updateData",resdata);
		},
		openSpace(){
			if (this.nonavigate) return;
			uni.navigateTo({
				url:"../../pages/user-space/user-space?userid="+this.item.userid
			})
		}
	}
}
